











































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import CStatistics from '@/components/links/CStatistics.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '代表値',
    },
  },
})
export default class Average extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataText: string = '';
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'standard_normal_distribution_100',
      caption: '標準正規分布（大きさ100）',
      content: StatisticsCalc.generateSamples(100, 0, 1).map((value: number): number => { return Math.round(value * 10000) / 10000; }).join('\n'),
    },
    {
      name: 'standard_normal_distribution_1000',
      caption: '標準正規分布（大きさ1000）',
      content: '"' + StatisticsCalc.generateSamples(1000, 0, 1).map((value: number): number => { return Math.round(value * 10000) / 10000; }).join('", "') + '"',
    },
    {
      name: 'standard_normal_distribution_10000',
      caption: '標準正規分布（大きさ10000）',
      content: StatisticsCalc.generateSamples(10000, 0, 1).map((value: number): number => { return Math.round(value * 10000) / 10000; }).join(','),
    },
    {
      name: 'test',
      caption: 'センター試験（国語）',
      content: StatisticsCalc.generateSamples(256, 111.29, 33.10).map((value: number): number => { return Math.round(value); }).join(' '),
    },
  ];
  private dataSamples: number[] = [];
  private dataSampleSize: string = '';
  private dataMaximum: string = '';
  private dataMinimum: string = '';
  private dataMean: string = '';
  private dataMode: string = '';
  private dataMedian: string = '';
  private dataVariance: string = '';
  private dataStandardDeviation: string = '';
  private dataKSigmaIntervals: {
    _1sigmaInterval: string,
    _2sigmaInterval: string,
    _3sigmaInterval: string,
  } = {
    _1sigmaInterval: '',
    _2sigmaInterval: '',
    _3sigmaInterval: '',
  };

  private created(): void {
  }

  // Methods
  private listenOnSampleExampleSelect($event: Event): void {
    this.dataSelectedSampleExampleIndex = Number(<any>$event);
    this.dataText = this.dataSampleExamples[this.dataSelectedSampleExampleIndex].content;
  }
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private calcStatistics(): void {
    this.dataSamples.length = 0;
    let samples: string[] | null = this.dataText.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    if (samples !== null) {
      let decimalPartCounts: number[] = [];
      samples.forEach((value: string): void => {
        this.dataSamples.push(Number(value));
        decimalPartCounts.push(MathUtil.countDecimlPart(Number(value)));
      });
      let decimalPartCount: number = StatisticsCalc.calcMaximum(decimalPartCounts);
      this.dataSampleSize = String(MathUtil.toLocaleString(this.dataSamples.length));
      this.dataMean = MathUtil.toFixed(StatisticsCalc.calcMean(this.dataSamples), decimalPartCount + 2);
      let modes: number[] = StatisticsCalc.calcMode(this.dataSamples);
      this.dataMode = modes.length === 0 ? '--' : modes.join(' ');
      this.dataMedian = MathUtil.toFixed(StatisticsCalc.calcMedian(this.dataSamples), decimalPartCount);
      this.dataMaximum = String(StatisticsCalc.calcMaximum(this.dataSamples));
      this.dataMinimum = String(StatisticsCalc.calcMinimum(this.dataSamples));
      this.dataVariance = MathUtil.toFixed(StatisticsCalc.calcVariance(this.dataSamples), decimalPartCount + 2);
      this.dataStandardDeviation = MathUtil.toFixed(StatisticsCalc.calcStandardDeviation(this.dataSamples), decimalPartCount + 2);
      let kSigmaIntervals: {
        _1sigmaInterval: number,
        _2sigmaInterval: number,
        _3sigmaInterval: number,
      } = StatisticsCalc.calcKSigmaIntervals(this.dataSamples);
      this.dataKSigmaIntervals = {
        _1sigmaInterval: MathUtil.toFixed(kSigmaIntervals._1sigmaInterval * 100, 2),
        _2sigmaInterval: MathUtil.toFixed(kSigmaIntervals._2sigmaInterval * 100, 2),
        _3sigmaInterval: MathUtil.toFixed(kSigmaIntervals._3sigmaInterval * 100, 2),
      };
    }
  }
}
